* {
  font-family: sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}
body {
  text-align: center;
  background-image: linear-gradient(#64dfdf, #80ffdb);
  height: 100vh;
}
.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main-heading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 15rem;
  width: 100%;
  color: #fff;
  font-family: "Chonburi", cursive;
  color: #289292;
  z-index: -1;
  opacity: 0.15;
}
.search {
  padding: 50px 10px;
  width: 70%;
}
.search input {
  width: 70%;
  border: 2px solid #3badad;
  font-size: 1.1rem;
}
.search input::placeholder {
  font-size: 0.9rem;
}
.search button {
  width: 20%;
  background-color: #3badad;
  border: 2px solid #3badad;
  color: #fff;
  font-size: 1.1rem;
  cursor: pointer;
  transition: 0.5s;
}
.search button:hover {
  background-color: #289292;
  border: 2px solid #289292;
}
.search input,
.search button {
  padding: 15px;
  outline: none;
}
.result {
  width: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 50px 0 rgba(31, 38, 135, 0.35);
  backdrop-filter: blur(7px);
  border-radius: 10px;
  padding: 70px 50px;
  text-align: left;
}
.result h1 {
  font-size: 2rem;
  color: #187979;
}
.result h3 {
  color: #3badad;
  font-size: 5rem;
  margin: 10px 0 30px;
}
.result p {
  font-size: 1.05rem;
  color: #0b4e4e;
}
@media only screen and (max-width: 1024px) {
  .main-heading {
    font-size: 12rem;
  }
}
@media only screen and (max-width: 768px) {
  .main-heading {
    font-size: 9rem;
  }
}
@media only screen and (max-width: 480px) {
  .main-heading {
    display: none;
  }
  .search {
    padding: 30px 10px;
    width: 100%;
  }
}
@media only screen and (max-width: 380px) {
  .search button {
    width: 30%;
  }
  .result {
    width: 300px;
  }
}
